import React from 'react';
import styles from './styles.module.scss';
function List({list}) {
  return (
    <ul className={styles['school-list']}>
      {list.map(item => (
        <li key={item.id}>
          <div className={styles['img-box']}>
            <img src={item.img} alt=""/>
          </div>
          <div className={styles['cont-box']}>
            <p>{item.tit}</p>
            <p>{item.num}</p>
          </div>
        </li>
      ))}
    </ul>
  );
}
export default List;
